@charset "UTF-8";



/* --------------------------------
    common
-------------------------------- */

body {
    font-family: 'Inconsolata', ;
}

.in {
    max-width: 1000px;
    margin: 0 auto;
    position: relative;

    @include media-mobile {
        max-width: 100%;
    }
}


/* --------------------------------
	blog-header
-------------------------------- */

.blog-header {
    @include clearfix;
    padding: 20px 100px;

    @include media-mobile {
        padding: 30px 0 32px;
        text-align: center;
    }

    h1 {
        float: left;
        font-size: 24px;
        position: relative;

        @include media-mobile {
            float: none;
        }

        a {
            display: block;
            color: #000;
        }

        &:before {
            content: " ";
            display: block;
            position: absolute;
            bottom: 4px;
            left: -20px;
            width: 10px;
            height: 4px;
            background: #e8edf3;
            border-radius: 2px;
        }
    }

    nav {
        float: right;
        font-size: 0;
        margin-top: 5px;

        @include media-mobile {
            float: none;
            margin-top: 30px;
        }

        a {
            display: inline-block;
            color: #000;
            text-transform: uppercase;
            font-size: 16px;
            letter-spacing: .05em;
            font-weight: bold;
            position: relative;

            &:before {
                content: " ";
                display: block;
                position: absolute;
                bottom: 50%;
                left: 0;
                margin-bottom: -1px;
                background: #000;
                height: 2px;
                width: 0;
                transition: all .25s $ease;

                @include media-mobile {
                    transition: none;
                }
            }

            &:hover {
                &:before {
                    width: 100%;

                    @include media-mobile {
                        width: 0;
                    }
                }
            }

            &:not(:first-child) {
                margin-left: 40px;
            }

            @include media-mobile {
                font-size: 14px;

                &:not(:first-child) {
                    margin-left: 30px;
                }
            }
        }
    }
}



/* --------------------------------
    blog-footer
-------------------------------- */

.blog-footer {
    padding: 60px 100px;
    text-align: center;

    @include media-mobile {
        padding: 60px 0;
    }

    .sns {
        font-size: 0;

        li {
            display: inline-block;
            margin: 0 20px;

            &:hover {
                a {
                    i {
                        opacity: 1;
                        transform: rotateY(360deg);
                    }
                }
            }

            @include media-mobile {
                margin: 0 16px;
            }

            a {
                font-size: 28px;
                color: #000;

                i {
                    opacity: .2;
                    transition: all .25s $ease;
                }

                @include media-mobile {
                    font-size: 18px;
                }
            }
        }
    }

    small {
        display: inline-block;
        font-size: 14px;
        margin-top: 30px;
        letter-spacing: .05em;

        @include media-mobile {
            margin-top: 30px;
        }

        &:before {
            content: " ";
            display: block;
            width: 40px;
            height: 2px;
            background: #eee;
            margin: 0 auto 35px;

            @include media-mobile {
                margin-bottom: 30px;
            }
        }

        i {
            font-size: 12px;
        }

        span {
            display: block;
            margin-top: 1em;

            > i {
                color: #e74c3c;
            }

            a {
                text-decoration: underline;
                color: #777;

                &:hover {
                    text-decoration: none;
                }
            }
        }
    }
}



/* --------------------------------
    blog-search
-------------------------------- */

.blog-search {
    display: none;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 19999;
    white-space: nowrap;

    @include media-mobile {
        position: absolute;
        top: 160px;
        transform: translate(-50%,0);
    }

    > form {
        background: #23241f;
        padding: 1em;
        font-size: 14px;
        font-family: monospace, monospace;
        color: #fff;

        &:before {
            content: "$ search ";
        }

        > input {
            width: 400px;
            -webkit-appearance: none;
               -moz-appearance: none;
                    appearance: none;
            outline: transparent;
            @include media-mobile {
                width: 200px;
            }
        }
    }
}






